<template>
<b-overlay :show="overlay" rounded="sm">
    <div id="app">
        
        <div class="view">
            <b-button :class="[ data_type === 'all' ? 'selected' : '']" class="text-capitalize" @click="change_source('all')">All</b-button>
            <b-button :class="[ data_type === 'current' ? 'selected' : '']" class="text-capitalize" @click="change_source('current')">Current</b-button>
            <b-button :class="[ data_type === 'history' ? 'selected' : '']" class="text-capitalize" @click="change_source('history')">History</b-button>
            <b-button class="text-capitalize" @click="getData">Load Data</b-button>
            <h6 class="tweet_date">Last Load : {{nowdate}}</h6>
        </div>
        
        <div v-if="(data_type=='all' || data_type=='current') && !hasitems(jsondata.current)">
            <h3 class="tweet_title list_title">Current</h3>
            <div class="tweet">
                No data!
            </div>
        </div>

        <div v-if="(data_type=='all' || data_type=='current') && hasitems(jsondata.current)">
            <h3 class="tweet_title list_title">Current</h3>
            <div class="tweet" v-for="item in jsondata.current" v-bind:key="item.id">
                <div class="box">
                    <article class="media">
                        <div class="media-left">
                            <figure class="image">
                                <img src="">
                            </figure>
                        </div>
                        <div class="media-content">
                            <div class="content">
                                <p><strong>{{item.service_name}}</strong> <small>{{item.service}}</small></p>
                                <p>{{item.summary}}</p>
                            </div>
                        </div>
                    </article>
                </div>
            </div>
        </div>

        <div v-if="(data_type=='all' || data_type=='history') && !hasitems(jsondata.archive)">
            <h3 class="tweet_title list_title">History</h3>
            <div class="tweet">
                No data!
            </div>
        </div>

        <div v-if="(data_type=='all' || data_type=='history') && hasitems(jsondata.archive)">
            <h3 class="tweet_title list_title">History</h3>
            <div class="tweet" v-for="item in jsondata.archive" v-bind:key="item.id">
                <div class="box">
                    <article class="media">
                        <b-row>
                            <b-col class="content" cols="10">
                                <p><strong>{{item.service_name}}</strong></p>
                                <p>Service : {{item.service}}</p>
                                <p>Date : {{ $moment(item.date, 'X').format('YYYY-MM-DD HH:mm:ss Z') }}
                                <p>{{item.summary}}</p>
                            </b-col>
                            <b-col cols="2" class="padding-left-0">                               
                                <b-button v-b-modal="item.date" class="text-capitalize"><b-icon icon="zoom-in"></b-icon></b-button>
                                <b-modal :id="item.date" size="md" scrollable hide-header ok-only centered header-bg-variant="dark" header-text-variant="light"><span id="modalhtml" v-html="item.description"></span></b-modal>

                            </b-col>
                        </b-row>
                    </article>
                </div>
            </div>
        </div>
        <div >
            <a class="ScrollOnTop btn btn-light" @click="ScrollOnTop" v-show="visible_scroll">
                <b-icon-arrow-up></b-icon-arrow-up>
            </a>
        </div>
        


    </div>
</b-overlay>
</template>

<script>

import DataList from '../util/getData'

export default {
	name: 'DataList',
	components: {
        
	},
    data() {
        return {
            jsondata:{},
            data_type: 'all',
            overlay: false,
            nowdate: '',
            visible_scroll: false
        }
    },
    mounted() {
        this.getData()
        window.addEventListener('scroll', this.scrollListener)
    },
    beforeDestroy() {
        window.removeEventListener('scroll', this.scrollListener)
    },
    methods: {
        async getData() {
            this.toggle_overlay(true)
            this.jsondata = await DataList.get_data()
            this.nowdate = this.date()
            console.log("component:",this.jsondata)
            this.toggle_overlay(false)
        },
        date() {
            return this.$moment().format('YYYY-MM-DD HH:mm:ss Z')
        },
        change_source(data_type) {
            this.data_type = data_type;
            console.log(this.data_type);
        },
        hasitems(data) {
            try {
                if(data.length > 0) return true
                else return false
            } catch {
                return false
            }
        },
        toggle_overlay(value) {
            this.overlay = value
        },
        ScrollOnTop() {
            window.scroll(0, 0)
        },
        scrollListener(e) {
            this.visible_scroll = window.pageYOffset > 150
        }
    }
}
</script>

<style>
    .greybg {
        background-color: #eeeeee;
    }

    .whitebg {
        background-color: #ffffff;
    }

    .yellowbg {
        background-color: #fcfdef;
    }

    .yellowfg {
        color: #997744;
    }

    .greyfg {
        color: #444444;
    }

    .orangefg {
        color: #dd6622;
    }

    #modalhtml div {
        margin-bottom: 10px;
    }

    .list_title {
        font-weight: 800;
        line-height: 1.2;
    }
    .btn {
        border-radius: 8px;
        background-color: rgba(0, 0, 0, 0.55);
        padding-top: 27px;
        padding-left: 10px;
        padding-right: 10px;
        padding-bottom: 5px;
    }
    .ScrollOnTop {
        position: fixed;
        right: 0px;
        margin-right: 20px;
        top: 90vh;
    }
</style>